<template>
    <div>
    <el-container>
        <el-header class="home_header">
            <div class="title">TODO</div>
            <el-dropdown class="user_info" @command="commandHandler">
                <span class="el-dropdown-link" style="color: #ffffff">
                    {{user.username}}<i class="el-icon-user-solid el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="userInfo">个人中心</el-dropdown-item>
                    <el-dropdown-item command="setting">设置</el-dropdown-item>

                    <el-dropdown-item command="logout" divided>注销</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </el-header>
        <el-container style="height:610px">
            <el-aside width="240px" >
                <div style="height:100%;background-color:#545c64">
                <el-menu background-color="#545c64"
                        text-color="#fff"
                        active-text-color="#ffd04b"  router>
                    <el-submenu :index="index+''" v-for="(item,index) in routes" v-if="!item.hidden" :key="index">
                        <template slot="title">
                            <i :class="item.iconCls"></i>
                            <span>{{item.name}}</span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item :index="child.path" v-for="(child,index) in item.children" :key="index">{{child.name}}</el-menu-item>
                        </el-menu-item-group>

                    </el-submenu>

                </el-menu>
                </div>
            </el-aside>
            <el-main>
                <el-breadcrumb separator="/"  v-if="this.$router.currentRoute.path != '/home'">
                    <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item>{{this.$router.currentRoute.name}}</el-breadcrumb-item>
                </el-breadcrumb>
                <div class="hello_welcome" v-if="this.$router.currentRoute.path == '/home'">
                    hello welcome
                </div>
                <router-view class="home_view"/>
            </el-main>
        </el-container>
    </el-container>
    </div>
</template>

<script>
    export default {
        name: "home",
        data() {
            return{
                user: JSON.parse(window.sessionStorage.getItem("user"))
            }
        },
        computed: {
            routes() {
                return this.$store.state.routes;
            }
        },
        methods: {
            // menuClick(index){
            //     this.$router.push(index)
            // },
            commandHandler(item){
                if("logout" == item){
                    this.$confirm('此操作将注销, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.getRequest("/logout");
                        window.sessionStorage.removeItem("user");
                        this.$store.commit('initRoutes',[])
                        this.$router.replace("/")
                        // this.$message({
                        //     type: 'success',
                        //     message: '注销成功!'
                        // });
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消注销'
                        });
                    });
                }
            }
        }
    }
</script>

