<template>
    <div>
        <el-form :rules="rules"
                 ref="loginForm"
                 :model="loginForm"
                 class="loginContainer"
                 v-loading="loading"
                 element-loading-text="正在登录..."
                 element-loading-spinner="el-icon-loading"
                 element-loading-background="rgba(0, 0, 0, 0.8)">
            <h2 class="loginTitle">
                系统登陆
            </h2>
<!--            <el-form-item prop="username">-->
<!--                <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder="请输入用户名"></el-input>-->
<!--            </el-form-item>-->
            <el-form-item prop="email">
                <el-input v-model="loginForm.email" placeholder="请输入邮箱地址"></el-input>
            </el-form-item>

            <el-form-item prop="password">
                <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="请输入密码" @keydown.enter.native="submitLogin"></el-input>
            </el-form-item>
            <el-checkbox v-model="checked" class="loginRem">
            </el-checkbox>
            <div style="margin-bottom: 10px;text-align: right"><el-link type="primary"  @click="resetPwd" >忘记密码</el-link></div>
            <el-button type="primary" style="width: 100%;" @click="submitLogin">登陆</el-button>
        </el-form>

    </div>
</template>

<script>
    //import {postKeyValueRequest} from "../utils/api";

    export default {
        name: "Login",
        data(){
            return {
                loginForm:{
                    email: 'baichaolumeng@gmail.com',
                    password: '123456'
                },
                checked: true,
                rules:{
                    email: [
                        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                        { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }],
                    password: [{required: true, message: '请输入密码', trigger: 'blur'}]
                },
                // rules: {
                //     username:[{required: true, message: '请输入用户名', trigger: 'blur'} ],
                //     password:[{required: true, message: '请输入密码', trigger: 'blur'} ]
                // },
                loading: false
            }
        },
        methods: {
            resetPwd(){
                //to="{ path: '/home' }"
                window.open('/#/resetPwdFirst')
                //window.sessionStorage.setItem("forgetPwdUser",this.loginForm.username);
               // this.$router.forward('/#/resetPwd');

            },
            submitLogin() {
                this.$refs.loginForm.validate((valid) => {
                    if (valid){
                        this.loading = true
                        let lgform = {
                            username:this.loginForm.email,
                            password:this.loginForm.password
                        }

                        this.postKeyValueRequest("/doLogin",lgform).then(rsp=>{
                            this.loading = false
                            if(rsp){
                                alert(JSON.stringify(rsp));
                                window.sessionStorage.setItem('user',JSON.stringify(rsp));
                                let path = this.$route.query.redirect
                                this.$router.push((path == "/" || path == undefined) ? "/home" : path);
                            }
                        })
                    } else{
                        this.$message.error("请填写有效字段")
                        return false
                    }
                })
            }
        }
    }
</script>

<style>
    .loginContainer{
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        border: 1px solid #eaeaea;
        padding: 35px 35px 15px 35px;
        width: 350px;
        background-color: aliceblue;
        box-shadow: 0 0 25px #cac6c6;
    }
    .loginTitle{
        margin: 10px auto 30px auto;
        text-align: center;
        color: #505458;
    }
    .loginRem{
        text-align: left;
        margin: 0px 0px 20px 0px;
    }

</style>