<template>
    <div>
        <el-form :rules="rules"  class="loginContainer"
                 ref="resetForm"
                 :model="resetForm"
                 v-loading="loading"
                 element-loading-text="正在核实用户名..."
                 element-loading-spinner="el-icon-loading"
                 element-loading-background="rgba(0, 0, 0, 0.8)">

            <h2 class="loginTitle">
                重置密码(第一步)
            </h2>
            <el-form-item prop="email">
                <el-input type="text" v-model="resetForm.email"
                          auto-complete="off"
                          placeholder="请输入邮箱地址"></el-input>
            </el-form-item>
            <Vcode :show="isShow"
                    @onSuccess="onSuccess"
                    @onClose="onClose"/>
<!--            <el-form-item prop="VerificationCode">-->
<!--                <el-input v-model="resetPwdFrom.verificationCode"-->
<!--                          style="width: 230px"-->
<!--                          placeholder="请输入验证码" ></el-input>-->
<!--                <el-button @click="getVfCode">获取验证码</el-button>-->
<!--            </el-form-item>-->

            <el-button type="primary" style="width: 100%;" @click="submitLogin">下一步</el-button>
        </el-form>

    </div>
</template>

<script>
    //import {postKeyValueRequest} from "../utils/api";
    import Vcode from "vue-puzzle-vcode";
    export default {
        name: "ResetPwdFirst",
        mounted() {
            //this.resetPwdFrom.username = window.sessionStorage.getItem("forgetPwdUser")
        },
        data(){
            return {
                resetForm:{
                    email:'',
                },
                isShow: false,
                checked: true,
                rules: {
                    email:[{ required: true, message: '请输入邮箱地址', trigger: 'blur' },
                              { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }],
                },
                loading: false,
                resetUrl:'/',
                tag : false
            }
        },
        components:{
            Vcode
        },
        methods: {
            // 用户通过了验证
            onSuccess(msg){
                this.isShow = false; // 通过验证后，需要手动隐藏模态框
                this.loading = true
                this.getRequest("/resetPassword/?email="+this.resetForm.email).then(rsp=>{
                    this.loading = false
                    if(rsp){
                        this.$router.push({path:'/resetPwdSecond'});
                        window.sessionStorage.setItem('forgetPwdUser',rsp.obj.toString());
                        //this.$router.push((path == "/" || path == undefined) ? "/home" : path);
                    }//else{
                    //     this.$message.error("用户不存在")
                    // }
                })
            },
            // 用户点击遮罩层，应该关闭模态框
            onClose(){
                this.isShow = false;
            },
            // getVfCode(){
            //     //to="{ path: '/home' }"
            //     //this.$router.push({path: '/resetPwd', query: {from:'update',email:this.loginForm.username}});
            //
            //     window.open('/resetPwd')
            //
            // },
            submitLogin() {
                    this.$refs.resetForm.validate((valid) => {
                        if (valid){
                            this.isShow = true;
                        } else{
                            this.$message.error("请填写所有字段")
                            return false
                        }
                    })


            }
        }
    }
</script>

