<template>
    <div>
        <el-form :rules="rules"
                 ref="resetForm"
                 :model="resetForm"
                 status-icon
                 class="loginContainer"
                 v-loading="reset_loading"
                 :element-loading-text="loading_text"
                 element-loading-spinner="el-icon-loading"
                 element-loading-background="rgba(0, 0, 0, 0.8)">
            <h2 class="loginTitle">
                重置密码(第二步)
            </h2>
            <el-form-item prop="username">
                <el-input type="text" v-model="resetForm.username" auto-complete="off" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item prop="verificationCode">
                <el-input v-model="resetForm.verificationCode"
                          style="width: 230px"
                          placeholder="请输入验证码" ></el-input>
                <el-button @click="getVfCode" :disabled="disable">获取邮箱验证码</el-button>
            </el-form-item>

            <el-button type="primary" style="width: 100%;" @click="submitLogin">下一步</el-button>
        </el-form>

    </div>
</template>

<script>
    //import {postKeyValueRequest} from "../utils/api";

    export default {
        name: "ResetPwdSecond",
        mounted() {
            this.resetForm.username = window.sessionStorage.getItem("forgetPwdUser");

            //this.resetPwdFrom.username = window.sessionStorage.getItem("forgetPwdUser")
        },
        data(){
            var checkCode = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('验证码不能为空'));
                }else{
                    callback();
                }

            };
            var checkName = (rule, value, callback) => {
                if (!value) {
                    return callback(new Error('用户名不能为空'));
                }

            };
            return {
                resetForm:{
                    username: '',
                    verificationCode: ''
                },
                checked: true,
                reset_loading: false,
                resetUrl:'/',
                rules: {
                   // username: [{validator: checkCode, trigger: 'blur'}],
                    verificationCode: [{validator: checkCode, trigger: 'blur'}]
                },
                disable:false,
                vfCode:'',
                loading_text:''
            }
        },
        methods: {
            getVfCode(){
                this.disable = true
                this.loading_text = "正在获取验证码..."
                this.reset_loading = true;
                this.getRequest("/resetPassword/getVfCode?username="+this.resetForm.username).then(rsp=>{
                    this.reset_loading = false;
                    if(rsp){
                        this.vfCode = rsp.obj.toString();
                        //this.$message.success("发送成功，请登录邮箱查看")
                        this.disable = false;

                    }
                })
                //to="{ path: '/home' }"
                //this.$router.push({path: '/resetPwd', query: {from:'update',email:this.loginForm.username}});

            },
            submitLogin() {
                this.$refs.resetForm.validate((valid) => {
                    let _this = this;
                    if (valid){
                        if(this.resetForm.verificationCode == this.vfCode){
                            this.loading_text = "loading..."
                            this.reset_loading = true;
                            this.getRequest("/resetPassword/sendResetURL?username="+this.resetForm.username).then(rsp=>{
                                this.reset_loading = false;
                                this.resetForm.verificationCode = '';
                                this.vfCode = '';
                                this.$message({
                                    message: rsp.message,
                                    type: 'success',
                                    duration: 5000
                                })
                                setTimeout(function(){
                                    _this.$router.push("/")
                                },5000)

                            })
                        }else{
                            this.$message.error("验证码不正确，请重新输入");
                        }
                        // this.loading = true
                        // this.postKeyValueRequest("/doLogin",this.loginForm).then(rsp=>{
                        //     this.loading = false
                        //     if(rsp){
                        //         alert(JSON.stringify(rsp));
                        //         window.sessionStorage.setItem('user',JSON.stringify(rsp));
                        //         let path = this.$route.query.redirect
                        //         this.$router.push((path == "/" || path == undefined) ? "/home" : path);
                        //     }
                        // })
                    } else{
                         this.$message.error("请输入验证码")
                         return false
                    }
                })
            }
        }
    }
</script>

<style>
    .loginContainer{
        border-radius: 15px;
        background-clip: padding-box;
        margin: 180px auto;
        border: 1px solid #eaeaea;
        padding: 35px 35px 15px 35px;
        width: 350px;
        background-color: aliceblue;
        box-shadow: 0 0 25px #cac6c6;
    }
    .loginTitle{
        margin: 10px auto 30px auto;
        text-align: center;
        color: #505458;
    }
    .loginRem{
        text-align: left;
        margin: 0px 0px 20px 0px;
    }

</style>