<template>
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="loginContainer"
             v-loading="reset_pass_oading"
             element-loading-text="修改密码中..."
             element-loading-spinner="el-icon-loading"
             element-loading-background="rgba(0, 0, 0, 0.8)">
        <h2 class="loginTitle">
            重置密码(第三步)
        </h2>
        <el-form-item prop="username" label="用户名" >
            <el-input type="text" v-model="ruleForm.username" auto-complete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    export default {
        name: "ResetPwdThird",
        mounted() {
            let _this = this
            this.ruleForm.username = window.sessionStorage.getItem("forgetPwdUser");
            let  ha = this.$route.query.ha;
            console.log(ha);
            this.getRequest("/resetPassword/verification?ha="+ha).then(rsp=>{
                    if(rsp){
                        if(rsp.message == "ok" || rsp.message == null){
                            this.ruleForm.username = rsp.obj;
                        }else{
                            this.$message({
                                message: rsp.message,
                                type: 'error',
                                duration: 5000
                            });
                            this.$router.push("/resetPwdFirst")
                        }
                    }
                    // }else{
                    //     to.path = "/resetPwdFirst"
                    //     next()
                    //     //this.$router.push("/resetPwdFirst");
                    //     this.$message(rsp.message);
                    // }
            })
        },
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.ruleForm.pass) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                ruleForm: {
                    username:'',
                    pass: '',
                    checkPass: '',
                },
                reset_pass_oading:false,
                rules: {
                    pass: [
                        { validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { validator: validatePass2, trigger: 'blur' }
                    ],
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    this.reset_pass_oading = true
                    let _this = this;
                    if (valid) {
                        let user = {
                            username: this.ruleForm.username,
                            password: this.ruleForm.pass
                        }
                        this.putRequest("/resetPassword/",user).then(rsp => {
                            this.reset_pass_oading = false;
                            this.$message({
                                message: rsp.message,
                                type: 'success',
                                duration: 5000
                            });
                            setTimeout(function(){
                                _this.$router.push("/");
                            },5000)
                        })
                    } else {
                        this.$message("请输入所有字段");
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>

